*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Condensed', sans-serif;
}
body{
    background-color: rgb(240, 189, 196);
}


.header{
    text-align: center;
}
.h1{
    margin-top: 40px;
    font-weight: bolder;
    font-size: 2rem;
    color: darkblue;
    text-shadow: 0px 0px 1px brown;
}
.h2{
    font-weight: bolder;
    font-size: 2rem;
    color:darkblue;
    margin-bottom: 10px;
    text-shadow: 0px 0px 1px brown;
}
.head-span{
    font-style: italic;
    color:red;
    text-shadow: 0px 0px 1px brown;
}
.content-div{
    width: 80%;
    height: auto;
    margin: auto;
}
.header-content{
    text-align: center;
    color: brown;
    font-weight: bolder;
}

.info{
    margin-top: 10px;
    margin-bottom: 20px;
    height: auto;
    width: auto;
    background-color: wheat;
    border-radius: 40px;
    padding: 6px 10px;
    box-shadow: 1px 0px 5px red;
}

.info-span{
    width: 100px;
    margin: auto;
    font-weight: bolder;
}
.plus{
    text-align: center;
    cursor: pointer;
    font-size: 1.8rem;
    font-weight: bolder;
    width: 40px;
    background-color:darkmagenta;
    border: 2px solid black;
    color: white;
    border-radius: 40px;
    margin: auto;
}
.plus:hover{
    background-color: lightsalmon;
    border: 2px solid red;
    color:black;
}

.h-question{
    text-align: center;
    margin-bottom: 6px;
    margin-top: 10px;
    padding: 8px;
}
.h-answer{
    text-align: center;
    word-spacing: 2px;
    
}

